<template>
<main>
    <Header :header='content'/>
    <div class="container content">
            <form  action=""  class="info">
                    <div> 
                        <label>کد پرسنلی تحویل گیرنده</label> 
                        <input type="text">
                    </div>
                    <div> 
                        <label>کد پرسنلی مسئول دستگاه</label> 
                        <input type="text">
                    </div>  
                    <div> 
                        <label>کد دستگاه</label> 
                        <input type="text">
                    </div>  
                    <div> 
                        <label>کد ست دریافتی</label>
                        <p style="position:relative;width:30vw">
                            <input type="text">
                            <button class="btn btn-primary" style="position:absolute;left:0;top:0;bottom:0;width:5rem;">افزودن</button>
                        </p> 
                    </div>
                    <div> 
                        <label>نوع دستگاه</label>
                        <div class="radio-flex">
                            <div class="radio-container">
                                <label for="">Ultrasonic</label>
                                <input type="radio" name="status" value="true" class="radio">
                            </div> 
                            <div class="radio-container">
                                <label for="">Washer</label>
                                <input type="radio" name="status" value="false" class="radio">
                            </div> 
                        </div> 
                    </div>
                    <div>
                        <p class="fs-5">سیکل جاری دستگاه : {{counter}}</p>
                    </div>
                    <div class="btns">
                        <button class="btn btn-success" @click="showModal"  data-bs-toggle="modal" data-bs-target="#staticBackdrop">ثبت اطلاعات</button>
                        <button class="btn btn-success" @click="counterPlus">شروع فرآیند</button>
                    </div>  
            </form>

            <div class="list">
                <table>
                    <tr>
                        <td>
                            <div>ست شماره 1</div>
                            <button class="btn btn-primary" @click="showModal1"  data-bs-toggle="modal1" data-bs-target="#staticBackdrop1">مشاهده</button>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <div>ست شماره 2</div>
                            <button class="btn btn-primary" @click="showModal1"  data-bs-toggle="modal1" data-bs-target="#staticBackdrop1">مشاهده</button>
                        </td>
                    </tr>
                    
                    
                </table>
            </div>
            
    </div>

<!-- ****************مدال ثبت اطلاعات***************** -->
<div class="modal fade"  ref="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content" style="background-color:#F0FFFF;border:2px solid #2DA839">
      <div class="modal-header" style="border: none;">
        <img src="../../../assets/check.png" alt="" style="margin:auto;width:50px;height:50px;">
      </div>
      <div class="modal-body  fs-5" style="margin:auto;border:none"> 
        اطلاعات ست های دریافتی با موفقیت ثبت گردید
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button type="button" class="btn btn-primary mx-3"  data-bs-dismiss="modal">بستن</button>
      </div>
    </div>
  </div>
</div>
<!-- ****************مدال مشاهده***************** -->
<div class="modal fade"  ref="staticBackdrop1" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content" style="background-color:#FFFFFF;border:2px solid #3563B2;">
      <div class="modal-body" style="margin:auto;border:none;height:400px;width:90%;overflow-y:scroll;"> 
          <table class="table  table-striped">
                    <tbody>
                        <tr style="text-align:center;">
                        <td>Mark</td>
                        </tr>
                    </tbody>
            </table> 
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button type="button" class="btn btn-primary mx-3"  data-bs-dismiss="modal">بستن</button>
      </div>
    </div>
  </div>
</div>


</main>
</template>

<script>
import {Modal} from 'bootstrap'
import Header from '../../../components/Header.vue'

export default {
    name:'Machine',
    components:{Header},
    data() {
        return {
            content:'شستشو توسط دستگاه',
            counter:0,
            modal:null,
            modal1:null,
        }
    },
    mounted(){
      this.modal=new Modal(this.$refs.staticBackdrop);
      this.modal1=new Modal(this.$refs.staticBackdrop1);
    },
    methods: {
      
      showModal(){
        this.modal.show()
      },
      showModal1(){
        this.modal1.show()
      },
      counterPlus(){
        this.counter+=1;
          
      }
    },
}
</script>

<style lang="scss" scoped>
@import '../../../style/form-list.scss'
</style>